.descriptif-content {
  @include externalPaddingMobile;

  @include desktop {
    @include externalPaddingDesktop;
  }
}

.descriptif-content__banner {
  overflow: hidden;
  height: 255px;
  border-radius: 25px;

  @include desktop {
    height: 415px;
  }
}

.description-content__center {
  display:flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  margin-top: 19px;
  margin-bottom: 50px;

  @include desktop {
    margin-top: 31px;
    flex-direction: row;
  }
}

.content-center__information-location {
  width: 100%;
  height: 140px;
  color: $primary-color;

  & p {
    font-weight: 500;
    padding-bottom: 10px;
  }

  & p:nth-child(1) {
    font-size: 18px;
  }

  & p:nth-child(2) {
    font-size: 14px;
  }

  @include desktop {
    width: 70%;
    height: 170px;

    & p {
      padding-bottom: 10px;
    }

    & p:nth-child(1) {
      font-size: 36px;
    }

    & p:nth-child(2) {
      font-size: 18px;
      padding-bottom: 23px;
    }
  }
}

.content-center__information-user {
  width: 100%;
  background-color: yellow;

  @include desktop {
    width:30%;
  }
}



.description-dropDown {
  width: 100%;
  height: auto;
  margin-top: 19px;

  & li,
  p {
    font-size: 12px;
  }

  & button {
    height: 30px;
    font-size: 13px;
  }


  @include desktop {
    margin-top: 31px;

    & li,
    p,
    button {
      font-size: 18px;
    }

    & button {
      height: 52px;
    }

    display: grid;
    grid-template-columns: 47% 47%;
    justify-content:space-between;
  }
}