.content-tags {
    width: 84px;
    height: 18px;
    font-weight: 500;
    font-size: 10px;
    color: $primary-font-button;
    background-color: $primary-background-button;
    border-radius: 5px;
    text-align: center;
    line-height: 18px;

    & span {
        font-size: 10px;
    }

    @include desktop {
        width: 115px;
        height: 25px;
        border-radius: 10px;
        line-height: 25px;

        & span {
            font-size: 14px;
        }
    }

}